body {
    background: #06071B;;
    color: var(--primary-text-color);
    font-family: 'Inter', sans-serif;
    
}
img{
    display: block;
    max-width: 100%;
    height: auto;
}

a {
    text-decoration: none;
    padding: 0px;
    margin: 0px;
}





ul {
        list-style: none;
        padding: 0px;
        margin: 0px;
    }

    h1,
    h2,
    h3,
    h4,
    h5,
    h6,
    p,
    ul {
        margin: 0;
        padding: 0;
    }